import uniPopup from '@/components/uni-popup/uni-popup.vue'
import simpleModal from '@/components/simple-pro/customModal.vue';
import uParse from '@/components/gaoyia-parse/parse.vue';
import uniSection from '@/components/uni-section/uni-section.vue';
import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue';
var _self;
export default {
	data() {
		return {
			numberValue:1,
			minQuantity:1,
			kucunshuliang:999,
			goid:0,
			itemss: '',
			show: false,
			leixing: '件',
			shuzi: 0,
			pickUp: false,
			pickUpAddress: '',
			ispintuan: '售',
			id: '',
			type: 0,
			loadGoods: false,
			specClass: 'none',
			miaosha: 0,
			goods: [],
			goodsorder: [],
			shopCartBg: false,
			scrollTop: false,
			taocanqingdan: false,
			good_type_name: '单品',
			navBarButton: false,
			navsel: 1,
			isCollect: 0,
			secondKillId: 0,
			secondKillTimeId: 0,
			copyTklStatus: false,
			ccsd: '<p></p>',
			guige_id: 0,
			guige: '',
			guigePrice: '',
			options: [{
					icon: 'home-filled',
					text: '回首页',
					color: '#ffa200'
				},
				{
					icon: 'headphones',
					text: '客服',
					color: '#646566'
				},
				{
					icon: 'starhalf',
					text: '去收藏',
					color: '#646566'
				}

			],
			buttonGroup: [{
				text: '立即购买',
				backgroundColor: '#ffa200',
				color: '#fff'
			}],
			specList: [{
				id: 1,
				name: '规格'
			}],
			specChildList: []
		};
	},
	components: {
		uniPopup,
		simpleModal,
		uParse,
		uniSection,
		uniGoodsNav
	},

	onShow: function() {
		this.copyTklStatus = false;
	},
	onLoad: function(e) {
		_self = this;
		if (e.id) {
			this.id = e.id;
			this.type = e.type;
			if (e.miaosha == 1) {
				this.miaosha = e.miaosha;
				this.secondKillId = e.id;
				this.secondKillTimeId = e.type;
			}
			console.log('id=' + e.id)
			console.log('type=' + e.type)
			console.log('miaosha=' + e.miaosha)
			this.loadGoodsInfo();
		} else {
			console.log(e)
			console.log(e.scene)
			var scene = decodeURIComponent(e.scene).split('&')
			console.log(scene)
			var id = scene[0].split('=')[1] // 3736
			var type = scene[1].split('=')[1] // 3736
			var miaosha = scene[2].split('=')[1] // 3736
			console.log('id=' + id)
			console.log('type=' + type)
			console.log('miaosha=' + miaosha)
			if (scene && id && type) {
				this.id = id;
				this.type = type;
				if (miaosha == 1) {
					this.miaosha = miaosha;
					this.secondKillId = id;
					this.secondKillTimeId = type;
				}

				this.loadGoodsInfo();
			} else {
				uni.navigateBack();
			}
		}
	},

	onPageScroll: function(e) {
		if (e.scrollTop > 200) {
			this.scrollTop = true;
		} else {
			this.scrollTop = false;
		}
	},
	onNavigationBarButtonTap: function() {
		this.navBarButton = !this.navBarButton;
	},
	methods: {
		loadGoodsInfo: function() {
			console.log(this.miaosha);
			if (this.miaosha == 1) {
				var goodsinfo = 'second_kill/detail';
				var vals = {
					id: this.id,
					secondKillTimeId: this.type
				};
				var xxx = this.$Request.topost1(goodsinfo, vals);
			} else {
				var goodsinfo = this.$api.goods.goodsinfo;
				var vals = {
					productId: this.id,
					type: this.type
				};
				var xxx = this.$Request.toget1(goodsinfo, vals);
			}
			xxx.then(res => {
				console.log(res);
				if (res.code == 1000) {
					// if (res.data.yprice == undefined) {
					// 	res.data.yprice = res.data.price - res.data.coupon_price;
					// }
					this.goods = res.content;
					this.loadGoods = true;
					this.ccsd = res.content.detail;
					this.isCollect = this.goods.isCollect;
					this.pickUp = this.goods.pickUp;
					this.pickUpAddress = this.goods.pickUpAddress;
					this.minQuantity = this.goods.commodityList[0].minQuantity;
					this.kucunshuliang = this.goods.stock;
					
					console.log('库存：'+this.kucunshuliang)
					
					if (this.goods.orders != null && this.goods.orders != undefined && this.goods.orders != [] && this.goods.orders
						.length > 0) {
						this.shuzi = this.goods.orders.length;
					}
					if (this.goods.type <= 0) {
						this.id = res.content.productId;
					} else {
						this.id = res.content.id;
					}
					console.log('价格', res.content.discountPrice + 'this.goods.type' + this.goods.type + 'this.miaosha' + this.miaosha)
					this.type = res.content.type;
					if (this.miaosha == 1) {
						this.guigePrice = res.content.discountPrice;
					} else {
						if (this.goods.type == 0) {
							this.guigePrice = res.content.discountPrice;
						} else {
							this.guigePrice = res.content.returnValue;
							this.guigePrice = res.content.discountPrice;
						}
					}

					if (this.goods.orders.length > 0) {
						if (this.goods.orders.length % 2 != 0) {
							this.goodsorder = this.goodsorder.concat(this.goods.orders)
							this.goodsorder = this.goodsorder.concat(this.goods.orders)
						} else {
							this.goodsorder = this.goodsorder.concat(this.goods.orders)
						}
						let len = this.goodsorder.length;
						let n = 2; //假设每行显示4个
						let lineNum = len % n === 0 ? len / n : Math.floor( (len / n) + 1 );
						let res = [];
						for (let i = 0; i < lineNum; i++) {
							let temp = this.goodsorder.slice(i * n, i * n + n);
							res.push(JSON.parse(JSON.stringify(temp)));
						}
						console.log(res);
						this.goodsorder = res;
						console.log(this.goodsorder);

					}



					console.log('规格价格', this.guigePrice)
					if (this.goods.isCollect == 1) {
						_self.options = [{
								icon: 'home-filled',
								text: '回首页',
								color: '#ffa200'
							}, {
								icon: 'headphones',
								text: '客服',
								color: '#646566'
							},
							{
								icon: 'star-filled',
								text: '已收藏',
								color: '#ffa200'
							}
						];
					}
					console.log('aaaaa', this.goods.type);
					this.buttonGroup = [{
						text: '立即购买',
						backgroundColor: '#ffa200',
						color: '#fff'
					}];

					// 0周期狗1单品2套餐,-1单品团购,-2套餐团购,-3周期狗团购
					if (this.goods.type == 0) {
						this.good_type_name = '周期购';
						this.ispintuan = '售'
					} else if (this.goods.type == 1) {
						this.good_type_name = '单品';
						this.ispintuan = '售'
					} else if (this.goods.type == 2) {
						this.good_type_name = '套餐';
						this.ispintuan = '售'
					} else if (this.goods.type == -1) {
						// this.good_type_name = '单品团购';
						if (this.goods.minAskType == 0) {
							this.good_type_name = this.goods.minAsk + '人成团';
						} else {
							this.good_type_name = this.goods.minAsk + '件成团';
						}
						this.ispintuan = '拼'
						this.taocanqingdan = true;
						this.buttonGroup = [{
								text: '单独购买',
								backgroundColor: '#ffa200',
								color: '#fff'
							},
							{
								text: '开团',
								backgroundColor: '#ff0000',
								color: '#fff'
							}
						];
					} else if (this.goods.type == -2) {
						this.good_type_name = '套餐团购';
						this.ispintuan = '拼'
						this.taocanqingdan = true;
						this.buttonGroup = [{
								text: '单独购买',
								backgroundColor: '#ffa200',
								color: '#fff'
							},
							{
								text: '开团',
								backgroundColor: '#ff0000',
								color: '#fff'
							}
						];
					} else if (this.goods.type == -3) {
						this.good_type_name = '周期购团购';
						this.ispintuan = '拼'
						this.taocanqingdan = true;
						this.buttonGroup = [{
								text: '单独购买',
								backgroundColor: '#ffa200',
								color: '#fff'
							},
							{
								text: '开团',
								backgroundColor: '#ff0000',
								color: '#fff'
							}
						];
					} else {
						this.ispintuan = '售'
						this.good_type_name = '单品';
					}


					if (this.type != 0) {
						this.goods.commodityList.forEach(item => {
							if (this.miaosha == 1) {
								var x = {
									id: item.id,
									name: item.name,
									groupBuyPrice: item.groupBuyPrice,
									imageUrl: item.imageUrl,
									price: item.secondKillPrice,
									pid: 1
								}
							} else {
								var x = {
									id: item.id,
									name: item.name,
									groupBuyPrice: item.groupBuyPrice,
									imageUrl: item.imageUrl,
									price: item.price,
									pid: 1
								}
							}

							this.specChildList.push(x);
						});
					}

				} else {
					uni.navigateBack();
				}
			});
		},

		huishouye() {
			uni.switchTab({
				url: '/pages/index/index'
			});
		},

		shoucang() {

			var token = uni.getStorageSync('authorization');
			if (token == '' || token == null) {
				uni.showModal({
					cancelText: '取消登录',
					confirmText: '立即登录',
					title: '提示',
					content: '是否确定授权登录？',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '../myinfo/login'
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
				return
			}

			if (this.isCollect == 0) {
				this.$Request.topost(this.$api.goods.collect_deal, {
					productId: this.id,
					collect: true
				}).then(res => {
					console.log(res);
					if (res.code == 1000) {
						this.isCollect = 1;
						this.options = [{
								icon: 'home-filled',
								text: '回首页',
								color: '#ffa200'
							},
							{
								icon: 'headphones',
								text: '客服',
								color: '#646566'
							},
							{
								icon: 'star-filled',
								text: '已收藏',
								color: '#ffa200'
							}
						];
					}
				})
			} else {
				this.$Request.topost(this.$api.goods.collect_deal, {
					productId: this.id,
					collect: false
				}).then(res => {
					console.log(res);
					if (res.code == 1000) {
						this.isCollect = 0;
						this.options = [{
								icon: 'home-filled',
								text: '回首页',
								color: '#ffa200'
							},
							{
								icon: 'headphones',
								text: '客服',
								color: '#646566'
							},
							{
								icon: 'starhalf',
								text: '去收藏',
								color: '#646566'
							}
						];
					}
				})
			}
		},

